@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");
.Navbar_Container {
  display: flex;
  position: fixed;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  background-color: #ffffff;
  padding: 10px 0px;
  z-index: 100;
}

.active {
  color: red;
}

.Navbar_Container a {
  cursor: pointer;
  text-decoration: none;
  color: #000000;
  font-weight: bold;
}
.background_transparent {
  background: white;
}
.background_transparent a {
  color: #000000;
}
.Navbar_PhoneNumber {
  margin-right: 20px;
  order: 3;
}
.Navbar_logo {
  font-family: "Lobster", cursive;
}

@media (max-width: 1000px) {
  .Navbar_Container {
    padding: 20px 10px;
    display: flex;
  }
  .Navbar_login {
    margin-right: 20%;
  }

  .backgroundcolor {
    background-color: #ffffff;
  }
}
