@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Krona+One&family=Noto+Sans+JP:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: 'Balsamiq Sans', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


h1{
  padding: 0;
  margin:0px 0;
}

.BGImage{
  background-image:url(/static/media/AILogo.93fc764a.png);
  width: 100%;
  height: 600px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.Navbar_Container {
  display: flex;
  position: fixed;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  background-color: #ffffff;
  padding: 10px 0px;
  z-index: 100;
}

.active {
  color: red;
}

.Navbar_Container a {
  cursor: pointer;
  text-decoration: none;
  color: #000000;
  font-weight: bold;
}
.background_transparent {
  background: white;
}
.background_transparent a {
  color: #000000;
}
.Navbar_PhoneNumber {
  margin-right: 20px;
  order: 3;
}
.Navbar_logo {
  font-family: "Lobster", cursive;
}

@media (max-width: 1000px) {
  .Navbar_Container {
    padding: 20px 10px;
    display: flex;
  }
  .Navbar_login {
    margin-right: 20%;
  }

  .backgroundcolor {
    background-color: #ffffff;
  }
}

